@import url(https://fonts.googleapis.com/css2?family=Rock+Salt&family=Trade+Winds&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Ruthie&display=swap);
html {
  margin: 0;
  background-image: url(/static/media/backg.89504b62.jpg);
  background-size: 100% 100vh;
  height: 100%;
  width: 100%;
  display: inline-block;
  overflow: hidden;
}
body {
  margin: 0;
  /* background-image: url("./image-from-rawpixel-id-2421445-original.jpg"); */
  background-color: rgba(0, 0, 0, 0.247);
  background-size: 100% 100vh;
  background-repeat: no-repeat;
  width: 100%;
  height: 100%;
  display: inline-block;
  overflow: hidden;
}

.listening {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 60px;
  color: white;
  position: absolute;
  opacity: 0.7;
  right: 43%;
  /* right: 0; */

  top: 27%;
}

.habits {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 50px;
  color: white;
  opacity: 0.7;
  position: absolute;
  left: 51%;
  top: 35%;
}

.logInButton {
  position: absolute;
  font-family: Arial, Helvetica, sans-serif;
  width: 300px;
  height: 40px;
  border-radius: 50px;
  background-color: #1db954;
  border: none;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  top: 50%;
  opacity: 0.7;
  font-size: 15px;
}

.logInButton:hover {
  background-color: #7adf9e;
}

.title {
  background-color: black;
  opacity: 0.5;
  width: 500px;
  height: 350px;
  position: absolute;
  left: 0;
  right: 0;
  margin-left: auto;
  margin-right: auto;
  top: 20%;
  border-radius: 10px;
}

.LogIn {
  margin: auto;
  margin-top: 350px;
  text-align: center;
}

.profile {
  position: relative;
  height: 90vh;
  width: 95vw;
  margin: 5vh 2.5vw;
  background-color: rgba(0, 0, 0, 0.658);
}

.profileContainer {
  display: inline-block;
  height: 150px;
  width: 30vw;
  min-width: 350px;
}

.profilePicture {
  display: inline-block;
  width: 100px;
  height: 100px;
  border-radius: 100px;
  margin-left: 6vw;

  margin-top: 35px;
}

.name {
  position: absolute;
  display: inline-block;
  margin-left: 35px;
  margin-top: 40px;
  margin-bottom: 0px;

  color: white;
  font-family: Arial, Helvetica, sans-serif;
}

.followBox {
  position: absolute;
  display: inline-block;
  margin-left: 35px;
  margin-top: 70px;
  margin-bottom: 0px;

  text-align: center;
  color: rgba(255, 255, 255, 0.521);
  font-family: Arial, Helvetica, sans-serif;
}

.followTitle {
  font-size: 14px;
  font-weight: lighter;
  margin-bottom: 2px;
}

.follow {
  margin-top: 0px;
  font-size: 14px;
  font-weight: lighter;
}

.followLeft {
  margin-left: 115px;
}

.currentTitle {
  font-size: 90px;
  float: right;
  margin: 40px 5vw 0 0;
  width: 470px;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
}

.logOut {
  border-radius: 50px;
  width: 90px;
  position: absolute;
  display: inline-block;

  margin-top: 130px;
  margin-left: 60px;

  text-align: center;
}

@media only screen and (device-width: 768px) {
  .currentTitle {
    font-size: 65px;
  }
}

@media only screen and (max-width: 320px) {
  .currentTitle {
    font-size: 15px;
  }
}

li {
  list-style: none;
}

.upArrow {
  position: absolute;
  transform: rotate(80deg);

  bottom: 260px;
  transition: transform 0.5s ease-out;
}

.upArrow:hover {
  transform: rotate(60deg);
}

.downArrow {
  position: absolute;
  transform: scaleX(-1) rotate(260deg);
  bottom: 0px;
  transition: transform 0.5s ease-out;
}

.downArrow:hover {
  transform: scaleX(-1) rotate(240deg);
}

.buttons {
  position: absolute;
  bottom: 17vh;
  right: 5vw;
}

.slideshow {
  scroll-behavior: smooth;
  position: relative;
  border: 2px none rgba(255, 255, 255, 0.39);
  border-radius: 2px;
  margin: 5px auto;
  height: 65vh;
  width: 83vw;
  box-sizing: content-box;
  overflow: hidden;
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
}

.navigate {
  font-family: Arial, Helvetica, sans-serif;
  color: rgba(255, 255, 255, 0.39);
  font-size: 26px;
  position: absolute;
  bottom: 142px;
  left: 13px;
  transform: rotate(90deg);
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.item {
  position: relative;
  height: 100%;
  width: 100%;
  margin: 0px;
  -webkit-animation: fadein 2s;
          animation: fadein 2s;
}

.red {
  background-color: rgba(184, 15, 15, 0.267);
}

.blue {
  background-color: rgba(0, 0, 255, 0);
}

@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

.databar {
  display: flex;
  align-items: center; /* Vertical center alignment */
  justify-content: flex-start; /* Horizontal center alignment */

  height: 10%;
  width: 100%;
  border: 1px none rgba(255, 255, 255, 0.404);
  box-sizing: border-box;
}

.index {
  width: 50px;
  font-family: Arial, Helvetica, sans-serif;
  color: rgba(255, 255, 255, 0.39);
  font-size: 30px;
  margin: 0 15px;
}

.data {
  font-family: Arial, Helvetica, sans-serif;
  color: white;
  font-size: 25px;
}

.trackArtist {
  font-family: Arial, Helvetica, sans-serif;
  color: rgba(255, 255, 255, 0.39);
  font-size: 16px;
  margin: 10px 15px 0 15px;
}

.trackImage {
  width: 40px;
  height: 40px;
  margin-right: 20px;
}

.artistImage {
  border-radius: 100%;
  width: 40px;
  height: 40px;
  margin-right: 20px;
}

@media only screen and (min-device-width: 375px) and (max-device-width: 812px) {
  .data {
    font-size: 10px;
  }

  .trackArtist {
    font-size: 4px;
    margin: 5px 15px 0 5px;
  }
  .artistImage {
    margin-right: 10px;
    margin-left: 0px;
  }

  .trackImage {
    margin-right: 5px;
  }

  .index {
    margin: 0 5px;
    width: 20px;
    font-size: 20px;
  }
}

.buttonContainer {
  position: absolute;
  display: inline-flex;
  right: 6vw;
  top: 130px;
  color: white;
  font-family: Arial, Helvetica, sans-serif;
}

button {
  border: 1px solid white;
  margin: 0;
  background: none;
  color: white;
}

button:hover {
  background: white;
  color: black;
}

.buttonActive {
  background: white;
  color: black;
}

.buttonLeft {
  border-radius: 50px 0 0 50px;
}

.buttonRight {
  border-radius: 0 50px 50px 0;
}

